._ {
  position: relative;
  width: 12.8%;
  min-width: 12rem;
}

.label {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  width: 100%;
}

.icon {
  width: 1rem;
  height: 1rem;
  fill: var(--secondary-color);
  stroke: var(--secondary-color);
}

.overlay {
  top: 3.75rem;
  gap: 0;
  padding: 0.5rem 0;
}
